* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Nunito, sans-serif;
}

main.dark, header.dark {
  --background-color: #202c37;
  --text-color: white;
  --elements-color: #2b3945;
  --shimmer-background: #476480;
  --shimmer-background-p: #304354;
}

main, header {
  background-color: var(--background-color);
  color: var(--text-color);
  --elements-color: white;
  --background-color: white;
  --text-color: black;
  --shimmer-background: #bcbdbd;
  --shimmer-background-p: #949799;
}

main {
  padding: 24px;
}

a {
  color: inherit;
  text-decoration-line: none;
}

.header-container {
  z-index: 1;
  background-color: var(--elements-color);
  padding-inline: 24px;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 4px #0000001a;
}

.header-content {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin-inline: auto;
  display: flex;
}

.title {
  font-weight: 800;
}

.theme-changer {
  cursor: pointer;
}

.countries-container {
  margin-top: 16px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 32px;
  max-width: 1200px;
  margin-inline: auto;
  display: grid;
}

.country-cards-container {
  min-height: calc(100vh - 50px);
}

.country-card {
  background-color: var(--elements-color);
  border-radius: 8px;
  width: 250px;
  padding-bottom: 24px;
  transition: all .2s ease-in-out;
  display: inline-block;
  overflow: hidden;
  box-shadow: 0 0 8px #0003;
}

.country-card:hover {
  transform: scale(1.03);
  box-shadow: 0 0 16px #0003;
}

.card-title {
  margin-block: 16px;
  font-size: 24px;
}

.country-card-image {
  height: 160px;
  overflow: hidden;
}

.country-card img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.card-text {
  height: 50%;
  padding-inline: 16px;
}

.card-text p {
  margin-block: 8px;
}

.country-card img {
  width: 100%;
}

.search-filter-container {
  margin-inline: auto;
  justify-content: space-between;
  gap: 24px;
  max-width: 1200px;
  margin-top: 16px;
  margin-bottom: 48px;
  display: flex;
}

.search-container {
  color: var(--text-color);
  background-color: var(--elements-color);
  border-radius: 4px;
  width: 100%;
  max-width: 350px;
  padding-left: 24px;
  overflow: hidden;
  box-shadow: 0 0 8px #00000026;
}

.search-container input {
  background-color: inherit;
  width: 90%;
  color: inherit;
  border: none;
  outline: none;
  padding: 16px;
}

.search-container input::placeholder {
  color: #999;
}

.filter-by-region {
  background-color: var(--elements-color);
  width: 250px;
  color: var(--text-color);
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 16px;
  box-shadow: 0 0 8px #00000026;
}

.country-detail {
  height: calc(100vh - 80px);
  overflow-x: hidden;
}

.country-detail.dark .border-countries a {
  background-color: #3d3d3d6b;
}

@media (width <= 768px) {
  .countries-container {
    justify-content: center;
  }

  .title {
    font-size: 16px;
  }

  .header-content p {
    font-size: 12px;
  }

  .search-filter-container {
    flex-direction: column;
  }
}
